	module.exports = {"data":{"post":{"html":"<p>Today. we found that no, we weren't dreaming, indeed the layoffs were true, for reasons of cost-cutting, not communicated to the staff (I knew because our department talks to one another after hours) and chosen arbitrary (my department head was not informed, I'm sure he would have not chosen his most senior admin for the boot). They were not communicated in one-to-one meetings, as one would expect, but instead dropped anonymously into the employees' home mailboxes on Friday afternoon. And this is supposed to motivate us remaining employees to pull ourselves out of the crisis? Hardly. Stay-at-home fatherhood is looking better all the time. The mood is miserable.</p>","fields":{"title":"Layoffs Update","date":"21 May 2002","url":"https://papascott.de/2002-05-21-layoffs-update/"}}},"pathContext":{"slug":"/2002-05-21-layoffs-update/","prev":{"fields":{"title":"Fahrrad!","date":"20 May 2002","slug":"/2002-05-20-fahrrad/"}},"next":{"fields":{"title":"Is Everyone Watching TV? Or What?","date":"23 May 2002","slug":"/2002-05-23-is-everyone-watching-tv-or-what/"}}}}

